import {Button } from 'react-bootstrap';
import FormModal from "../../../components/Modal/FormModal";
import IMAGES from '../../../assets/images';
import { AiOutlineRight } from 'react-icons/ai';
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../../src/utils/path";


export default function ProfileSuccess({ children, ...rest }) {
  const { show, handleClose } = rest;
  const navigate = useNavigate();

  return (
    <div id='profileSuccess_modal' className=''>
      <FormModal
        className="modal-lg profileSuccess_modal"
        title=""
        handleClose={handleClose}
        show={show}
      >
        <div className='px-xs-5 px-0 pb-3'>
          <div className='d-flex flex-column align-items-center justify-content-start px-xs-5 px-3 mx-5 text-center'>
            <img className='mb-3' src={IMAGES.NOTES_COMPLETED_ICON} alt='booked' />
            <h3 className='fw-bold'>Profile Completed</h3>
            <p className=''>You successfully completed your profile</p>
            <Button  onClick={() => navigate(PATH.PATIENT_DASHBOARD)} className='px-4 primary_bg' size="md">Go To Dashboard <AiOutlineRight/></Button>
          </div>
        </div>
      </FormModal>
    </div>
  );
};

