import { Col, Row, Form, Button, Modal } from "react-bootstrap";
import { useDispatch } from "react-redux";
import {
  createPatientPharmacy,
  getAllPatientPharmacy,
} from "../../../app/features/patientPharmacy/patientPharmacy.slice";
import { useForm } from "react-hook-form";

export default function AddPharmacy({ children, ...rest }) {
  const { show, handleClose } = rest;
  const { register, handleSubmit } = useForm();

  const dispatch = useDispatch();

  const moveToNext = () => {
    const finalData = {
      pageNo: 0,
      size: 0,
      isPagination: false,
    };
    dispatch(getAllPatientPharmacy(finalData));
    handleClose();
  };

  function createPharmacyData(submittedData) {
    const finalData = {
      pharmacyId: 0,
      pharmacyName: submittedData?.pharmacyName,
      pharmacyLocation: submittedData?.pharmacyLocation,
      country: "",
      ig_xcord: "",
      ig_ycord: "",
    };
    dispatch(createPatientPharmacy({ finalData, moveToNext }));
  }
  return (
    <div>
      <Modal
        size="lg"
        onHide={handleClose}
        show={show}
        aria-labelledby="patient-pharmacy-modal"
        centered
      >
        <Form
          onSubmit={handleSubmit(createPharmacyData)}
          className="px-2 pb-3"
        >
          <Modal.Header closeButton>
            <Modal.Title className="prescription-modal-title">
              Add Pharmacy
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="p-0">
            <Row>
              <Col xl={12}>
                <Form.Group controlId="formDate">
                  <Form.Label className="fw-semibold fs-6 mb-0">
                    Pharmacy Name
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Pharmacy Name"
                    {...register("pharmacyName")}
                    className="my-2"
                  />
                </Form.Group>
                <Form.Group controlId="formDate">
                  <Form.Label className="fw-semibold fs-6 mb-0">
                    Address
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="2972 Westheimer Rd. Santa Ana, Illinois 85486"
                    {...register("pharmacyLocation")}
                    className="my-2"
                  />
                </Form.Group>
              </Col>
            </Row>
          </Modal.Body>
          <Modal.Footer className="p-0">
            <div className="mt-3 d-flex justify-content-end">
              <Button
                onClick={handleClose}
                className="me-2 px-3 secondary_bg"
                size="sm"
              >
                Cancel
              </Button>
              <Button className="px-3 primary_bg" size="sm" type="submit">
                Add Pharmacy
              </Button>
            </div>
          </Modal.Footer>
        </Form>
      </Modal>
    </div>
  );
}
