import { useEffect, useState } from "react";
import {
  Button,
  Card,
  Col,
  Dropdown,
  DropdownButton,
  Form,
  Row,
} from "react-bootstrap";
import { AiOutlineLeft, AiOutlineRight } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import ProfileLayout from "../../../components/ProfileLayout/CompleteProfile";
import { PATH } from "../../../utils/path";
import AddPharmacy from "./AddPharmacy";
import ProfileSuccess from "./ProfileSuccess";
import { useDispatch, useSelector } from "react-redux";
import {
  createPatientPharmacy,
  deletePatientPharmacy,
  getAllPatientPharmacy,
  getPatientPharmacyData,
} from "../../../app/features/patientPharmacy/patientPharmacy.slice";
import { useForm } from "react-hook-form";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { FaEdit } from "react-icons/fa";
import { BsThreeDotsVertical } from "react-icons/bs";
import { ConfirmationModal } from "../../../components/Modal/ConfirmationModal";

const Pharmacy = () => {
  const { register, handleSubmit, setValue, reset } = useForm();
  const userDetails = JSON.parse(localStorage.getItem("family_doc_app"));
  const [show, setShow] = useState(false);
  const [deleteId, setDeleteId] = useState();
  const [open, setOpen] = useState(false);
  const [selectedPharmacy, setSelectedPharmacy] = useState({});
  const handleClose = () => setShow(false);

  const [showSuccess, setShowSuccess] = useState(false);
  const [editing, setEditing] = useState(false);
  const handleShowSuccess = () => setShowSuccess(true);
  const handleCloseSuccess = () => setShowSuccess(false);
  const [showPharmacyId, setShowPharmacyId] = useState();

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { patientPharmacy } = useSelector((state) => state);
  const { user } = useSelector((state) => state.auth);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);

  useEffect(() => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, user?.userId]);

  const moveToNext = () => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
  };

  function createPharmacyData(submittedData) {
    const finalData = {
      pharmacyId: showPharmacyId ? showPharmacyId : 0,
      pharmacyName: submittedData?.pharmacyName,
      pharmacyLocation: submittedData?.pharmacyLocation,
      country: submittedData?.country,
      ig_xcord: "",
      ig_ycord: "",
      pharmacyEmail: submittedData?.pharmacyEmail,
      userId: userDetails?.userId
    };
    if (patientPharmacy?.patientPharmacyData?.pharmacyId) {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    } else {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    }
    const selectedPharmacyData = patientPharmacy?.allPatientPharmacyData?.find(
      (item) => item.pharmacyName === submittedData?.pharmacyName
    );

    if (selectedPharmacyData) {
      setValue("pharmacyLocation", selectedPharmacyData.pharmacyLocation);
      setValue("country", selectedPharmacyData.country);
      setSelectedPharmacy(selectedPharmacyData);
    }
  }
  const onCreateSuccess = () => {
    const finalData = {
      PatientId: user?.userId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
    reset();
  };
  function deletePharmacyData() {
    const finalData = {
      PatientId: deleteId,
    };
    dispatch(deletePatientPharmacy({ finalData, onCreateSuccess }));
  }

  return (
    <ProfileLayout className="pt-5">
      <div className="mx-auto w-75 medical_history patient-pharmacy">
        <Card className="h-100 w-75 mx-auto">
          <Card.Body className="mb-3 pt-0 px-0">
            <Form className="px-5" onSubmit={handleSubmit(createPharmacyData)}>
              <Row>
                <Col lg={12}>
                  <h3 className="fw-bold py-4 mb-0">Pharmacy</h3>
                  <Form.Group>
                    <span className="d-flex justify-content-between align-items-baseline">
                      <Form.Label className="fw-bold">Pharmacy Name</Form.Label>
                      {(patientPharmacy?.patientPharmacyData?.pharmacyId &&
                        editing === false) && (
                          <DropdownButton
                            id="dropdown-item-button"
                            title={
                              <BsThreeDotsVertical
                                className="float-right color-99"
                                size={25}
                              />
                            }
                            size="sm"
                            bsPrefix="moreIcon"
                            className="moreIcon edit-dropdown px-0"
                            direction="left"
                          >
                            <div className="set-dropdown-menu">
                              <Dropdown.Item
                                // as="button"
                                className="d-flex align-items-center"
                              >
                                <FaEdit
                                  size={18}
                                  style={{ color: "#1A1A1A" }}
                                />
                                <span
                                  className="px-2"
                                  style={{ fontSize: "14px", color: "#1A1A1A" }}
                                  onClick={() => {
                                    setEditing(true);
                                  }}
                                >
                                  Edit
                                </span>
                              </Dropdown.Item>
                              <Dropdown.Item
                                // as="button"
                                className="d-flex align-items-center"
                                onClick={() => {
                                  setDeleteId(user?.userId);
                                  setOpen(true);
                                }}
                              >
                                <RiDeleteBin5Fill
                                  size={18}
                                  style={{ color: "#FD2121" }}
                                />
                                <span
                                  className="px-2"
                                  style={{ fontSize: "14px", color: "#FD2121" }}
                                >
                                  Remove
                                </span>
                              </Dropdown.Item>
                            </div>
                          </DropdownButton>
                        )}
                    </span>
                    <Form.Control
                      list="pharmacyNameList"
                      defaultValue={
                        patientPharmacy?.patientPharmacyData?.pharmacyName ?? ""
                      }
                      {...register("pharmacyName")}
                      onChange={(e) => {
                        const selectedPharmacyData =
                          patientPharmacy?.allPatientPharmacyData?.find(
                            (item) => item.pharmacyName === e.target.value
                          );

                        if (selectedPharmacyData) {
                          setValue(
                            "pharmacyLocation",
                            selectedPharmacyData.pharmacyLocation
                          );
                          setValue("country", selectedPharmacyData.country);
                          setSelectedPharmacy(selectedPharmacyData);
                          setShowPharmacyId(selectedPharmacyData.pharmacyId);
                        } else {
                          setValue("pharmacyLocation", "");
                          setValue("country", "");
                          setSelectedPharmacy({});
                        }
                      }}
                      placeholder="Select Pharmacy Name"
                      size="lg"
                      className={`${!patientPharmacy?.allPatientPharmacyData && "bg-transparent"}`}
                      readOnly={
                        patientPharmacy?.patientPharmacyData?.pharmacyName &&
                        editing
                          ? false
                          : patientPharmacy?.patientPharmacyData?.pharmacyName
                          ? true
                          : false
                      }
                      disabled={!patientPharmacy?.allPatientPharmacyData}
                    />
                    <datalist id="pharmacyNameList">
                      {patientPharmacy?.allPatientPharmacyData?.map((item) => (
                        <option
                          key={item?.pharmacyId}
                          value={item?.pharmacyName}
                        >
                          {item.pharmacyName}
                          {item.pharmacyLocation}
                        </option>
                      ))}
                    </datalist>
                  </Form.Group>

                  <Form.Group>
                    <Form.Label className="fw-bold mt-2">Address</Form.Label>
                    <Form.Control
                      type="text"
                      size="lg"
                      placeholder="Select Address"
                      {...register("pharmacyLocation")}
                      defaultValue={
                        patientPharmacy?.patientPharmacyData?.pharmacyLocation ? patientPharmacy?.patientPharmacyData?.pharmacyLocation : ""
                      }
                      className={`${!patientPharmacy?.allPatientPharmacyData && "bg-transparent"}`}
                      readOnly={
                        patientPharmacy?.patientPharmacyData
                          ?.pharmacyLocation && editing
                          ? false
                          : patientPharmacy?.patientPharmacyData
                              ?.pharmacyLocation
                          ? true
                          : false
                      }
                      maxLength={250}
                      disabled={!patientPharmacy?.allPatientPharmacyData}
                    />
                  </Form.Group>
                  <Form.Group>
                    <Form.Label className="fw-bold mt-2">County</Form.Label>
                    <Form.Control
                      type="text"
                      size="lg"
                      placeholder="Country"
                      {...register("country")}
                      defaultValue={
                        patientPharmacy?.patientPharmacyData?.country ?? ""
                      }
                      readOnly={
                        patientPharmacy?.patientPharmacyData?.country && editing
                          ? false
                          : patientPharmacy?.patientPharmacyData?.country
                          ? true
                          : false
                      }
                      className={`${!patientPharmacy?.allPatientPharmacyData && "bg-transparent"}`}
                      maxLength={250}
                      disabled={!patientPharmacy?.allPatientPharmacyData}
                    />
                  </Form.Group>
                  <Form.Group>
                    <Form.Label className="fw-bold mt-2">Pharmacy Email</Form.Label>
                    <Form.Control
                      type="text"
                      size="lg"
                      placeholder="Enter Pharmacy Email"
                      {...register("pharmacyEmail")}
                      defaultValue={
                        patientPharmacy?.patientPharmacyData?.pharmacyEmail ? patientPharmacy?.patientPharmacyData?.pharmacyEmail : ""
                      }
                      className={`${!patientPharmacy?.allPatientPharmacyData && "bg-transparent"}`}
                      readOnly={
                        patientPharmacy?.patientPharmacyData
                          ?.pharmacyEmail && editing
                          ? false
                          : patientPharmacy?.patientPharmacyData
                              ?.pharmacyEmail
                          ? true
                          : false
                      }
                      maxLength={250}
                      disabled={!patientPharmacy?.allPatientPharmacyData}
                    />
                  </Form.Group>
                 
                  {(!patientPharmacy?.patientPharmacyData?.pharmacyId ||
                    editing === true) && (
                    <Button
                      className="px-3 mt-3 primary_bg float-right"
                      size="sm"
                      type="submit"
                    >
                      {patientPharmacy?.patientPharmacyData?.pharmacyId
                        ? "Update "
                        : "Add "}
                      Pharmacy
                    </Button>
                  )}
                </Col>
              </Row>
            </Form>
         
            <AddPharmacy show={show} handleClose={handleClose} />
            <ProfileSuccess
              show={showSuccess}
              handleClose={handleCloseSuccess}
            />
          </Card.Body>
        </Card>
        <div className="my-4 d-flex justify-content-end width_75 mx-auto">
          <Button
            onClick={() => navigate(PATH.MEDICAL_HISTORY)}
            className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 me-2 primary_outline"
            size="sm"
            variant="outline-primary"
          >
            {" "}
            <AiOutlineLeft className="fs-6 me-1" />
            Back
          </Button>
          <Button
            onClick={handleShowSuccess}
            className="d-flex align-items-center px-4 px-md-5 fs-5 py-2 primary_bg"
            size="sm"
            variant="primary"
          >
            Next <AiOutlineRight className="fs-6 ms-1" />
          </Button>
        </div>
      </div>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Pharmacy"}
        title={"this pharmacy"}
        removeFunc={deletePharmacyData}
      />
    </ProfileLayout>
  );
};

export default Pharmacy;
